import { Component } from '@angular/core';
import {  ViewController,ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-galeriaimg-list',
  templateUrl: 'galeriaimg-list.html',
})
export class GaleriaimgListPage {
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  permisom: any=[];
    generated = '';
    public hasFilter: boolean = false;
    idafiliado:any='na';
    idempresa:any;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor(public viewCtrl: ViewController,public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
   
   
    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
    
  }

  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  showFilter()     
  {

    let self = this;
    self.loading = true;
   
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Filtrar por estado:',
    buttons: [
      {
        text: 'Activos',
        handler: () => {
        
          this.items=this.items2
          this.items= this.items2.filter((v) => {
         
            if(v.estado ) {
              
              if (v.estado=='Activo') {
                return true;
              }
              return false;
              
            }
          });
          self.loading = false;
         
          this.hasFilter = true;
        }
      },
      {
        text: 'Inactivos',
        handler: () => {
          this.items=this.items2
          this.items= this.items2.filter((v) => {
            if(v.estado ) {
              self.loading = false;
              if (v.estado=='Inactivo') {
                return true;
              }
              return false;
              
            }
          });   
        }
      }
     
    ]
  });

  actionSheet.present();



  }
 

  


  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg(this.navParams.data.idempresa + '/todos','galeriaimgs').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'galeriaimgs').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('GaleriaimgCreatePage', { record : item,idempresa:this.navParams.data.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros() 
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('GaleriaimgCreatePage',{ record : null ,idempresa:this.navParams.data.idempresa},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros() 
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
   

  

   cancel() {
    this.viewCtrl.dismiss();   
  }
 

  dalink(op) {     

   

    //this.msj.showError3('Link QR',op);
    let prompt = this.alertCtrl.create({
      title: 'link Imagen',
      message:'', 
      inputs: [
        {
          name: '',
          value:this.regService.daserverenuso0()+'/api/galeriaimgs/' + op._id +'/imagen'
        }
      ],
      buttons: [
        {
          text: 'Ok'
        }
      ]
    });
 
    prompt.present();
  }

 
}



